export default {
  data() {
    return {
      payBy: "",
      department: "",
      classId: "",
      roll: "",
      purpose: "",
      amount: "",
      lilha_pay: "",
      receiptNo: "",
      date: "",
      scholarship: "",
      scholarship_type: "",
      purposes: "",
      classes: "",
      monthCount: "",
      errors: "",
    };
  },
  mounted() {
   // this.getPurpose();
    this.getClass();
  },
  methods: {
    getClass() {
      this.$axios.get("/accounts/class").then((res) => {
        this.classes = res.data;
      });
    },
    getPurpose() {
      this.$axios.get("/accounts/purpose/"+this.classId).then((res) => {
        this.purposes = res.data;
      });
    },
    takingCost() {
      this.$axios
        .post("/accounts/costs-taking", {
          department: this.department,
          class: this.classId,
          roll: this.roll,
          purpose: this.purpose,
          scholarship: this.scholarship,
          scholarship_type: this.scholarship_type,
          payBy: this.payBy,
          amount: this.amount,
          receiptNo: this.receiptNo,
          date: this.date,
          discount: this.discount,
          monthCount: this.monthCount,
        })
        .then((response) => {
          (this.payBy = ""),
            (this.department = ""),
            (this.classId = ""),
            (this.roll = ""),
            (this.purpose = ""),
            (this.amount = ""),
            (this.lilha_pay = ""),
            (this.receiptNo = ""),
            (this.date = ""),
            (this.scholarship = ""),
            (this.scholarship_type = ""),
            (this.monthCount = ""),
            this.$toaster.success(response.data.message);
        })
        .catch((error) => {
          if (error.response.status === 422) {
            if(error.response.data.message){
              this.$toaster.success(error.response.data.message);
            }
            this.errors = error.response.data.errors;
          } else {

          }
        });
    },
  },
  computed: {
    totalPay() {
      let amount = "";
      if (this.purposes.length > 0) {
        this.purposes.filter((purpo) => {
          if (purpo.id === this.purpose) {
            amount = parseInt(purpo.amount);
          }
        });
      }

      return amount;
    },
    getPurposeMonth() {
      let month = 0;
      if (this.purposes.length > 0) {
        this.purposes.filter((purpo) => {
          if (purpo.id === this.purpose) {
            month = parseInt(purpo.month_wise);
          }
        });
      }
      return month;
    },
  },
  layout:'Account-content',
};
